<!-- "" -->
<template>
    <div id="map"></div>
</template>

<script>
import  { mapActions, mapGetters }  from    'vuex'
export default {
    mounted: function () {
        this.setMap_height()
        this.setRegionPointList()                                                           // 
        this.setBottomBtnState(2)
    },
    methods: {
        setMap_height: function(){
            // 
            const   windowHeight = window.innerHeight;                                      //  
            const   bottomNav = document.getElementsByClassName('mu-paper')[0];             //  
            const   bottomNav_height = bottomNav.offsetHeight;                              //  
            let     map_height = windowHeight - bottomNav_height;                           //   
            const   objMap = document.getElementById('map');                                //  Map
            objMap.style.height = map_height + 'px';                                        //  Map
        }
        ,asyncLoadMap: function() {
            setTimeout(() => {
                const miniMap = new BMap.Map("map", {enableMapClick:false});                // Map()
                const miniMapPoint = new BMap.Point(120.292284,36.126949);                   // (  )
                miniMap.centerAndZoom(miniMapPoint,11);                                     // ,15
                miniMap.addControl(new BMap.NavigationControl({
                    anchor: BMAP_ANCHOR_TOP_RIGHT,
                    type: BMAP_NAVIGATION_CONTROL_SMALL
                }));
                /*    */
                // 
                let administrativeRegion_Arr = [];
                administrativeRegion_Arr = this.$store.state.regionPointList;                       // 
                let lastLevel;
                miniMap.addEventListener("zoomstart",function(){
                    lastLevel = this.getZoom();
                });
                miniMap.addEventListener("zoomend", function(){
                    let zoomLevel = this.getZoom();     //
                    if (zoomLevel >= 15){                                                           // 3:
                        console.log("3:");
                        addBuilding(BuildingModel,17);
                    }else if (zoomLevel >= 14){
                        console.log("2:");                                         // 
                        addRangeOverlay(businessCirclePoint,16);
                    }else if (zoomLevel >= 12){
                        console.log("1:(  )");                          // 
                        addRangeOverlay(administrativeRegion_Arr,14);
                        setRangeOverlayStyle()                                                      // 
                    }else{
                        if (!lastLevel < 12) {
                            console.log("1:(  )");
                            addRangeOverlay(administrativeRegion_Arr,14);                           // 
                        }
                    }
                });
                //    (  +  +  )
                const buildingOverlayArr = []                                                       // 
                const addRangeOverlay = ( ObjGroup,setZoom ) => {                                     // 1 + 2 
                    miniMap.clearOverlays()                                                         // 
                    for(let i=0; i<ObjGroup.length; i++) {
                        let arr  = new Object();
                            arr  = ObjGroup[i];
                        let code    = arr.code
                            ,text   = arr.name + "<br />" + arr.resourceAmount + ""                   // 
                            ,zoom   = setZoom;
                        let RangeOverlay = new rangeOverlay(
                            new BMap.Point(arr.latitude,arr.longitude),text,code,zoom
                        );
                        // console.log(''+ i);
                        miniMap.addOverlay(RangeOverlay);
                    }
                };
                // .2
                function rangeOverlay(point,text,code,zoom){
                    this._point = point;
                    this._text  = text;
                    this._code  = code;
                    this._zoom  = zoom;
                }
                rangeOverlay.prototype = new BMap.Overlay();
                rangeOverlay.prototype.initialize = function(map){
                    this._map = map;
                    var div = this._div = document.createElement("div");
                    div.setAttribute("id",this._code);
                    div.setAttribute("class","range-overlay");
                    div.style.zIndex = BMap.Overlay.getZIndex(this._point.lat);
                    // code
                    var code = this._code,   //
                        point = this._point,
                        zoom = this._zoom;
                    div.onclick = function businessCirclePoint(){
                        // Ajaxcode
                        map.setZoom(zoom);      // ,
                        // console.log("" + url);
                    }
                    var span = this._span = document.createElement("span");
                    div.appendChild(span);
                    div.getElementsByTagName("span")[0].innerHTML =  this._text;
                    div.onmouseover = function(){ this.style.zIndex = "9"; }
                    div.onmouseout = function(){ this.style.zIndex = "1"; }
                    map.getPanes().labelPane.appendChild(div);
                    return div;
                }
                rangeOverlay.prototype.draw = function(){
                    var map = this._map;
                    //  ( ,.. )
                    var pointA = {}
                    pointA.lat = this._point.lng
                    pointA.lng = this._point.lat
                    var pixel = map.pointToOverlayPixel(pointA)
                    this._div.style.left = pixel.x - 40 + "px"
                    this._div.style.top  = pixel.y - 40 + "px"
                }
                // 3  
                const addBuilding = ( ObjGroup,setZoom ) => {
                    miniMap.clearOverlays()                                                         // 
                    for (let i = 0; i < ObjGroup.length; i++) {
                        let buildingArr = new Object()
                            buildingArr = ObjGroup[i]
                        let zoom = setZoom                                                         // 
                        // 
                        let text = "" + buildingArr.priceBeginning +  " ",
                            mouseoverTxt = buildingArr.name + " " + buildingArr.resourceAmount + ""
                        buildingOverlayArr[i] = BuildingOverlay = new buildingOverlay(
                            new BMap.Point(buildingArr.latitude,buildingArr.longitude),text,mouseoverTxt,buildingArr.code,i,zoom    // i = 
                        )
                        miniMap.addOverlay(BuildingOverlay)
                        buildingOverlayArr[i] = BuildingOverlay
                    }
                };
                const setRangeOverlayStyle = () => {
                    // let overlayArr_Arr = document.getElementsByClassName('range-overlay')
                    // for(let i=0; i<overlayArr_Arr.length;i++){
                    //     overlayArr_Arr[i].setAttribute("class","range-overlay--big")
                    //     console.log('' + i)
                    // }
                    // console.log(administrativeRegion_Arr.length)
                    for(let i=0; i<administrativeRegion_Arr.length; i++){
                        let rangeOverlay_Obj = document.getElementById(administrativeRegion_Arr[i].code)
                        rangeOverlay_Obj.setAttribute('class','range-overlay--big')
                    }
                }
                // (  , 11 )
                addRangeOverlay(administrativeRegion_Arr,14)
            },50)
        }
        // : 
        ,setRegionPointList() {
            this.$store.dispatch({
                type: 'setRegionPointList',
                cityCode: this.$store.state.city.cityCode                                   // 
            })
        }
        //  (  +  +  )
        ,addOverlay() { }
        // 
        ,setBottomBtnState(state) {
            let bottomBtn_Arr = document.getElementsByClassName("mu-buttom-item")
            // 
            for( let i = 0; i < bottomBtn_Arr.length; i++ ) {
                bottomBtn_Arr[i].setAttribute("class","mu-buttom-item router-link-active")
            }
            bottomBtn_Arr[state].setAttribute("class","mu-buttom-item router-link-active mu-bottom-item-active")        // ""
        }
    }
    ,computed: mapGetters({
        getRegionPointList: 'getRegionPointList'
    })
    ,watch: {
        //  question 
        getRegionPointList: function () {
            this.asyncLoadMap()
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../sass/main'

#map
</style>



